.tb_ap_media_wrap img,
.tb_ap_media_wrap video,
.tb_ap_media_wrap iframe{
    width: 100%;
    height: 100%;
    object-fit: cover;
}

#focus_68 .overlay-main{
    position: absolute;
    top: 0px;
    width: 100%;
    height: 100%;
}

#focus_68.tb_ap_post_container{
    display: block;
}
#focus_68 .tb_ap_post_row{
    width: 100%;
    float: left;
}
#focus_68 .tb_ap_post_large{
    float: left;
    width: 40%;
}
#focus_68.embed .tb_ap_post_large{
    height: 100vh;
}
#focus_68 .tb_ap_post_small{
    float: left;
    width: 60%;
    overflow: hidden;
}
#focus_68 .tb_ap_post_small .tb_ap_post_wrapper{
    padding: 0px;
    width: 33.3333%;
    float: left;
    height: 50vh;
    overflow: hidden;
}
#focus_68 .tb_ap_post_large .tb_ap_post_wrapper{
    width: 100%;
    height: 100%;
    padding: 0px;
}
#focus_68 .tb_ap_post_wrapper .tb_ap_post_in,
#focus_68 .tb_ap_post_wrapper .tb_fc_media_wrap,
#focus_68 .tb_ap_post_wrapper .tb_ap_post_media_wrapp,
#focus_68 .tb_ap_post_wrapper .tb_ap_media_wrap,
#focus_68 .tb_ap_post_wrapper .tb_ap_media_wrap img{
    height: 100%;
}

#focus_68 .tb_ap_post_row:nth-child(even) .tb_ap_post_large{
    float: right;
}
#focus_68 .tb_ap_post_in:hover .overlay-main{
    display: none;
}
#focus_68.embed .tb_ap_post_in:hover .overlay-main{
    display: block;
    opacity: 1;
    visibility: visible;
    transform: scale(1);
}
#focus_68.embed .tb_ap_contant_wrapper{
    text-align: center;
}
#focus_68 .tb_ap_post_in:hover .zoom-in-effect .overlay-main,
#focus_68 .zoom-in-effect .overlay-main.start{
    display: inherit;
}
#focus_68 .zoom-in-effect .overlay-main.start .tb_ap_post_details{
    opacity: 1;
    visibility: visible;
}
#focus_68 .zoom-in-effect .overlay-main.start .tb_ap_post_overlay{
    opacity: 0.6;
    visibility: visible;
}
#focus_68 .tb_ap_contant_wrapper .tb-cTBfont-{
    text-align: center;
}
#focus_68 .tb_ap_post_small.cards_17 .tb_ap_post_wrapper{
    width: 25%;
    height: 25vh;
}
#focus_68 .tb_ap_post_small.cards_13 .tb_ap_post_wrapper{
    width: 33.3333%;
    height: 25vh;
}
#focus_68 .tb_ap_post_small.cards_10 .tb_ap_post_wrapper{
    width: 33.3333%;
    height: 34vh;
}
#focus_68 .tb_ap_post_small.cards_7 .tb_ap_post_wrapper{
    width: 33.3333%;
    height: 50vh;
}
#focus_68 .tb_ap_post_small.cards_5 .tb_ap_post_wrapper{
    width: 50%;
    height: 50vh;
}
#focus_68 .tb_ap_post_small .tb_ap_author_profile{
    height: 42px;
    width: 42px;
}
#focus_68 .tb_ap_post_small .tb_ap_author_profile img{
    width: 100%;
    height: 100%;
}
