.tb_ap_contant_wrapper .tb-cTBfont- div{
    word-wrap: break-word;
    overflow: hidden;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    word-break: break-all;
}

@media screen and (max-width: 1200px) {
    #focus_68 .tb_ap_post_large{
        width: 100%;
        height: 50vh;
    }
    #focus_68 .tb_ap_post_small{
        width: 100%;
    }
    #focus_68 .tb_ap_post_small .tb_ap_post_wrapper{
        width: 33.33% !important;
        /* height: 50vh !important; */
    }
    #focus_68 .tb_ap_post_large,
    #focus_68 .tb_ap_post_small{
        height: auto;
    }
    #focus_68 .tb-cTBfont- > div{
        font-size: 3vw !important;
    }
}
@media screen and (max-width: 767px) {
    #focus_68 .tb_ap_post_small .tb_ap_post_wrapper{
        width: 50% !important;
    }
    #focus_68 .tb-cTBfont- > div{
        font-size: 4vw !important;
    }
}

@media screen and (max-width: 576px) {
    #focus_68 .tb_ap_post_small .tb_ap_post_wrapper{
        width: 100%;
    }
}


#focus_68 .tb_ap_post_large .tb-cTBfont- > div {
    font-size:2vw;
    word-break: break-word;
}

#focus_68 .tb_ap_post_details .tb_ap_contant_wrapper{
    top: 50%;
    transform: translate(0, -60%);
    justify-content: center;
}
